<template>
  <Card :bordered="false" dis-hover>
    <Form ref="searchDiv" :label-width="80"></Form>
    <Row>
      <Col span="24" class-name="layout-table-tools" ref="toolsBar">
        <Row type="flex" justify="space-between">
          <Col span="6"
            ><Button type="primary" style="margin-right: 5px">
              <router-link tag="span" to="/column/add"
                ><Icon type="md-add-circle" /> 添加栏目</router-link
              >
            </Button></Col
          >
          <Col span="6" style="text-align:right"
            ><Button type="primary" style="margin-right: 5px">
              展开所有子栏目
            </Button></Col
          >
        </Row>
      </Col>

      <Col span="24" class-name="layout-table-list">
        <Table
          row-key="id"
          :size="tableOpts.size"
          :height="tableOpts.height"
          @on-selection-change="selectChange"
          ref="selection"
          :columns="tableCol"
          :data="tableData"
        >
        </Table>
        <Button @click="handleSelectAll()" style="margin-right: 5px"
          >批量操作</Button
        >
        <Button @click="batchdetele">删除</Button>
      </Col>
    </Row>
  </Card>
</template>
<script>
import { appParentModuleList } from '@/api/menu.js'
import { SERVER_CODE } from '@/utils/constants.js'
import { spinShowMixins } from '@/mixins/spin-mixins.js'
export default {
  mixins: [spinShowMixins],
  data() {
    return {
      selectList: [],
      tableCol: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '序号',
          type: 'index',
          width: 100,
          align: 'center'
        },
        {
          title: '栏目名称',
          key: 'name',
          tree: true
        },
        {
          title: '操作',
          key: 'action',
          width: 250,
          fixed: 'right',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h(
                'Button',
                {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.edit(params.index)
                    }
                  }
                },
                '编辑'
              ),
              h(
                'Button',
                {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.remove(params.index)
                    }
                  }
                },
                '删除'
              )
            ])
          }
        }
      ],
      tableData: [
        {
          id: 2,
          name: '一般贸易',
          children: []
        },
        {
          id: 3,
          name: '跨境电商',
          children: []
        },
        {
          id: 4,
          name: '外综服务',
          children: []
        }
      ]
    }
  },
  created() {
    //this.getData()
  },
  methods: {
    getData() {
      this.loading = true
      let params = {}
      appParentModuleList(params)
        .then(({ code, data, msg }) => {
          if (code === SERVER_CODE.CODE_200) {
            this.tableData = data.content

            this.selectList = [] //清空单选框选择数据
          } else {
            this.$Message.info(msg)
          }
        })
        .catch(error => {
          console.log(error)
        })
        .finally(() => {
          this.loading = false
        })
    },
    handleSelectAll() {
      //单选框全选与取消
      let self = this
      self.selectall = !self.selectall
      self.$refs.selection.selectAll(self.selectall)
    },
    batchdetele() {
      //批量删除
      if (this.selectList.length == 0) {
        this.$Message.info('请选择删除对象！')
      } else {
        this.tableData = []
        this.selectList = [] //清空单选框选择数据
      }
    },
    selectChange(res) {
      //选择框状态变化监听
      this.selectList = res
    },
    addChildren(param) {
      //跳转编辑页面
      console.log(param.index)
      console.log(param.row)
    },
    edit(index) {
      //跳转编辑页面
      console.log(this.tableData[index])
    },
    remove(index) {
      //删除一条数据
      this.$Modal.confirm({
        title: '确认删除',
        content: '<p>该操作将导致数据无法恢复，请谨慎操作！</p>',
        onOk: () => {
          console.log(index)
        },
        onCancel: () => {}
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.layout-table-tools {
  button {
    margin-right: 12px;
  }
}
</style>
